import React, { useState, useRef } from "react";
import {
  Box,
  Typography,
  Button,
  Grid,
  Dialog,
  IconButton
} from "@mui/material";
import PlayArrowIcon from "@mui/icons-material/PlayArrow";
import CloseIcon from "@mui/icons-material/Close";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";

const TestimonialsSection = () => {
  const [contentType, setContentType] = useState<"video" | "written">("video");
  const [openVideo, setOpenVideo] = useState<string | null>(null);
  const [showAll, setShowAll] = useState(false);
  const [currentVideoIndex, setCurrentVideoIndex] = useState(0);
  const scrollContainerRef = useRef<HTMLDivElement>(null);

  // 👉 Just put your 30 Vimeo IDs here
  const vimeoIds = [
    "123456789",
    "987654321",
    "456789123",
    "111222333",
    "444555666",
    "777888999",
    "101112131",
    "141516171",
    "181920212",
    "232425262",
    "272829303",
    "313233343",
    // ... add all 30 Vimeo IDs
  ];

  // Generate testimonials automatically
  const testimonials = vimeoIds.map((id, index) => ({
    id: index + 1,
    name: `Student ${index + 1}`,
    role: "Student",
    videoUrl: `https://player.vimeo.com/video/${id}`,
    thumbnail: `https://vumbnail.com/${id}.jpg`
  }));

  // Videos to show in horizontal scroll (3 at a time)
  const videosPerView = 3;
  const maxIndex = Math.max(0, testimonials.length - videosPerView);
  
  // Get current set of videos to display
  const currentVideos = testimonials.slice(currentVideoIndex, currentVideoIndex + videosPerView);

  // Scroll functions
  const scrollLeft = () => {
    setCurrentVideoIndex(Math.max(0, currentVideoIndex - 1));
  };

  const scrollRight = () => {
    setCurrentVideoIndex(Math.min(maxIndex, currentVideoIndex + 1));
  };

  return (
    <Box
      sx={{
        background:
          "linear-gradient(180deg, #70A6B2 0%, #70A6B2 50%, white 50%, white 100%)",
        py: { xs: 4, sm: 6, md: 8, lg: 10 },
        position: "relative"
      }}
    >
      <Box
        sx={{
          maxWidth: "1440px",
          width: "100%",
          mx: "auto",
          px: { xs: 2, sm: 3, md: 4, lg: 6 }
        }}
      >
        {/* Title */}
        <Typography
          variant="h2"
          sx={{
            textAlign: "center",
            fontSize: { xs: "1.5rem", sm: "2rem", md: "2.5rem", lg: "3rem" },
            fontWeight: 700,
            color: "white",
            mb: { xs: 3, sm: 4 },
            px: { xs: 2, sm: 0 }
          }}
        >
          Why Students Love One Window
        </Typography>

        {/* Content Toggle */}
        <Box sx={{ display: "flex", justifyContent: "center", mb: { xs: 4, sm: 5, md: 6 } }}>
          <Box
            sx={{
              background: "white",
              borderRadius: "25px",
              p: 1,
              display: "flex",
              boxShadow: "0 4px 20px rgba(0,0,0,0.1)",
              width: { xs: "90%", sm: "auto" },
              maxWidth: { xs: "300px", sm: "none" }
            }}
          >
            <Button
              variant={contentType === "video" ? "contained" : "text"}
              onClick={() => setContentType("video")}
              sx={{
                borderRadius: "20px",
                px: { xs: 2, sm: 3 },
                py: 1,
                fontSize: { xs: "0.9rem", sm: "1rem" },
                fontWeight: 600,
                textTransform: "none",
                flex: { xs: 1, sm: "none" },
                background:
                  contentType === "video" ? "#FF6B35" : "transparent",
                color: contentType === "video" ? "white" : "#2C3E50",
                "&:hover": {
                  background:
                    contentType === "video"
                      ? "#E55A2B"
                      : "rgba(255, 107, 53, 0.05)"
                }
              }}
            >
              Video
            </Button>
            <Button
              variant={contentType === "written" ? "contained" : "text"}
              onClick={() => setContentType("written")}
              sx={{
                borderRadius: "20px",
                px: { xs: 2, sm: 3 },
                py: 1,
                fontSize: { xs: "0.9rem", sm: "1rem" },
                fontWeight: 600,
                textTransform: "none",
                flex: { xs: 1, sm: "none" },
                background:
                  contentType === "written" ? "#FF6B35" : "transparent",
                color: contentType === "written" ? "white" : "#2C3E50",
                "&:hover": {
                  background:
                    contentType === "written"
                      ? "#E55A2B"
                      : "rgba(255, 107, 53, 0.05)"
                }
              }}
            >
              Written
            </Button>
          </Box>
        </Box>

        {/* Video Cards */}
        {contentType === "video" && (
          <>
            {/* Navigation Arrows and Video Container */}
            <Box sx={{ position: "relative", width: "100%" }}>
              {/* Left Arrow */}
              <IconButton
                onClick={scrollLeft}
                disabled={currentVideoIndex === 0}
                sx={{
                  position: "absolute",
                  left: { xs: -10, sm: -20 },
                  top: "50%",
                  transform: "translateY(-50%)",
                  zIndex: 2,
                  background: "white",
                  boxShadow: "0 4px 20px rgba(0,0,0,0.1)",
                  width: { xs: 40, sm: 50 },
                  height: { xs: 40, sm: 50 },
                  "&:hover": {
                    background: "#f5f5f5"
                  },
                  "&:disabled": {
                    opacity: 0.3
                  }
                }}
              >
                <ArrowBackIosIcon sx={{ fontSize: { xs: "1rem", sm: "1.2rem" } }} />
              </IconButton>

              {/* Right Arrow */}
              <IconButton
                onClick={scrollRight}
                disabled={currentVideoIndex >= maxIndex}
                sx={{
                  position: "absolute",
                  right: { xs: -10, sm: -20 },
                  top: "50%",
                  transform: "translateY(-50%)",
                  zIndex: 2,
                  background: "white",
                  boxShadow: "0 4px 20px rgba(0,0,0,0.1)",
                  width: { xs: 40, sm: 50 },
                  height: { xs: 40, sm: 50 },
                  "&:hover": {
                    background: "#f5f5f5"
                  },
                  "&:disabled": {
                    opacity: 0.3
                  }
                }}
              >
                <ArrowForwardIosIcon sx={{ fontSize: { xs: "1rem", sm: "1.2rem" } }} />
              </IconButton>

              {/* Video Cards Container */}
              <Box
                ref={scrollContainerRef}
                sx={{
                  display: "flex",
                  gap: { xs: 2, sm: 3 },
                  overflow: "hidden",
                  px: { xs: 4, sm: 6 },
                  justifyContent: "center"
                }}
              >
                {currentVideos.map((testimonial) => (
                  <Box
                    key={testimonial.id}
                    sx={{
                      background: "white",
                      borderRadius: "20px",
                      overflow: "hidden",
                      boxShadow: "0 8px 30px rgba(0,0,0,0.12)",
                      transition: "all 0.3s ease",
                      cursor: "pointer",
                      minWidth: { xs: "280px", sm: "320px" },
                      maxWidth: { xs: "280px", sm: "320px" },
                      flex: "0 0 auto",
                      "&:hover": {
                        transform: "translateY(-8px)",
                        boxShadow: "0 12px 40px rgba(0,0,0,0.15)"
                      }
                    }}
                    onClick={() => setOpenVideo(testimonial.videoUrl)}
                  >
                    {/* Thumbnail (Lazy load) */}
                    <Box
                      component="img"
                      src={testimonial.thumbnail}
                      alt={`${testimonial.name} testimonial`}
                      loading="lazy"
                      sx={{
                        width: "100%",
                        height: { xs: "180px", sm: "200px" },
                        objectFit: "cover"
                      }}
                    />

                    {/* Play button overlay */}
                    <Box
                      sx={{
                        position: "absolute",
                        top: "50%",
                        left: "50%",
                        transform: "translate(-50%, -50%)",
                        background: "rgba(0,0,0,0.6)",
                        borderRadius: "50%",
                        p: 2
                      }}
                    >
                      <PlayArrowIcon
                        sx={{ fontSize: { xs: "2.5rem", sm: "3rem" }, color: "white" }}
                      />
                    </Box>

                    {/* Content */}
                    <Box sx={{ p: { xs: 2, sm: 3 } }}>
                      <Typography
                        variant="h6"
                        sx={{
                          fontWeight: 600,
                          color: "#2C3E50",
                          mb: 1,
                          fontSize: { xs: "1rem", sm: "1.125rem" }
                        }}
                      >
                        {testimonial.name}
                      </Typography>
                      <Typography
                        variant="body2"
                        sx={{
                          color: "#7F8C8D",
                          fontSize: { xs: "0.8rem", sm: "0.875rem" }
                        }}
                      >
                        {testimonial.role}
                      </Typography>
                    </Box>
                  </Box>
                ))}
              </Box>
            </Box>

            {/* Video Counter */}
            <Box sx={{ textAlign: "center", mt: { xs: 3, sm: 4 } }}>
              <Typography
                variant="body2"
                sx={{
                  color: "#7F8C8D",
                  fontSize: "0.875rem",
                  mb: 2
                }}
              >
                {currentVideoIndex + 1} - {Math.min(currentVideoIndex + videosPerView, testimonials.length)} of {testimonials.length} testimonials
              </Typography>
            </Box>
          </>
        )}

        {/* Written Content */}
        {contentType === "written" && (
          <Box
            sx={{
              background: "white",
              borderRadius: "20px",
              p: 4,
              textAlign: "center",
              boxShadow: "0 8px 30px rgba(0,0,0,0.12)"
            }}
          >
            <Typography
              variant="h5"
              sx={{ color: "#2C3E50", mb: 3 }}
            >
              Written testimonials coming soon...
            </Typography>
            <Typography
              variant="body1"
              sx={{ color: "#7F8C8D", lineHeight: 1.6 }}
            >
              We're working on collecting written testimonials from our
              students. Check back soon!
            </Typography>
          </Box>
        )}

        {/* Video Popup Modal */}
        <Dialog
          open={!!openVideo}
          onClose={() => setOpenVideo(null)}
          maxWidth="md"
          fullWidth
        >
          <Box sx={{ position: "relative", pb: "56.25%", height: 0 }}>
            <iframe
              src={openVideo || undefined}
              frameBorder="0"
              allow="autoplay; fullscreen; picture-in-picture"
              allowFullScreen
              style={{
                position: "absolute",
                top: 0,
                left: 0,
                width: "100%",
                height: "100%"
              }}
            ></iframe>
          </Box>
          <IconButton
            onClick={() => setOpenVideo(null)}
            sx={{
              position: "absolute",
              top: 10,
              right: 10,
              color: "white"
            }}
          >
            <CloseIcon />
          </IconButton>
        </Dialog>
      </Box>
    </Box>
  );
};

export default TestimonialsSection;
